define('frontend/index/route', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].Route.extend({

        beforeModel: function beforeModel() {
            var self = this;
            Ember['default'].run.later(function () {
                self.transitionTo('login');
            }, 2000);
        }

    });

});